vm._v("Moluscos")]),_c('p',{staticClass:"ps-2 mb-0 mt-2"},[_vm._v("Los moluscos, del latín mollis (blando), son los segundos más abundantes después de los artrópodos. Este grupo abarca desde formas de vida muy simples y microscópicas, hasta los invertebrados más complejos como el calamar gigante (Architeutbis), almejas gigantes (Tridacna gigas). Entre las especies que lo conforman se encuentran los invertebrados más lentos, pero también los más activos de todos, sus hábitos alimenticios van desde la herbívora hasta las especies carnívoras y depredadoras, filtradoras macrofágicas, detritívoras y también parásitas. Están en hábitats bastante variados desde charcas, lagos, aguas corrientes, charcos marinos costeros y mar abierto, la mayoría de ellos se encuentran en el océano. El filo de los moluscos está conformado por caracoles, babosas, mariposas de mar, almejas, mejillones, ostras, calamares, pulpos, entre otros.")])])]),_c('div',{staticClass:"col-lg-4"},[_c('figure',[_c('img',{attrs:{"src":require("@/assets/curso/temas/Tema1/img5.png")}})])])]),_c('div',{staticClass:"row justify-content-center align-items-center my-lg-4"},[_c('div',{staticClass:"col-lg-8"},[_c('div',{staticClass:"number",attrs:{"data-number":"03"}},[_c('b',{staticClass:"ps-2"},[_vm._v("Equinodermos")]),_c('p',{staticClass:"ps-2 mb-0 mt-2"},[_vm._v("Los equinodermos son animales marinos. Su nombre hace referencia a las espinas encontradas en su parte externa, que forman placas características que los diferencian de otros invertebrados. Son los animales más comunes en los mares profundos, carecen de cabeza, poseen pocos órganos sensoriales y se dividen en: lirios de mar, estrellas de mar, erizos de mar, dólares de arena y pepinos de mar.")])])]),_c('div',{staticClass:"col-lg-4"},[_c('figure',[_c('img',{attrs:{"src":require("@/assets/curso/temas/Tema1/img8.png")}})])])]),_c('div',{staticClass:"row justify-content-center align-items-center my-lg-4"},[_c('div',{staticClass:"col-lg-8"},[_c('div',{static