endo el desarrollo en tierra seca. Los reptiles tienen piel dura, seca y escamosa que los protege tanto de los daños físicos como de la desecación, la piel está provista de células portadoras de pigmentos llamados cromatóforos que proporcionan a la mayoría de los lagartos y las serpientes llamativos colores. Poseen mandíbulas muy fuertes que les permiten aplicar fuerza sobre sus presas. Tienen un diseño corporal más eficaz con patas diseñadas para el desplazamiento sobre tierra, están representados por las tortugas, lagartos, serpientes, cocodrilos y culebrillas ciegas.")])])]),_c('div',{staticClass:"col-lg-4"},[_c('figure',[_c('img',{attrs:{"src":require("@/assets/curso/temas/Tema1/img7.png")}})])])]),_c('div',{staticClass:"row justify-content-center align-items-center my-lg-4"},[_c('div',{staticClass:"col-lg-8"},[_c('div',{staticClass:"number",attrs:{"data-number":"06"}},[_c('b',{staticClass:"ps-2"},[_vm._v("Aves")]),_c('p',{staticClass:"ps-2 mb-0 mt-2"},[_vm._v("Las aves son el grupo de vertebrados más estudiado, el más fácil de observar y el más numeroso. Se encuentran en bosques, desiertos, montañas, praderas y en los océanos. Se diferencian de los demás vertebrados por la presencia de plumas y extremidades modificadas en forma de alas que les permiten sostenerse y propulsarse, y patas adaptadas para la marcha, natación o para posarse sobre las ramas. Todas poseen picos y ponen huevos, tienen un sistema respiratorio altamente eficaz y un sistema nervioso bien desarrollado con sentidos muy agudos.")])])]),_c('div',{staticClass:"col-lg-4"},[_c('figure',[_c('img',{attrs:{"src":require("@/assets/curso/temas/Tema1/img9.png")}})])])]),_c('div',{staticClass:"row justify-content-center align-items-center my-lg-4"},[_c('div',{staticClass:"col-lg-8"},[_c('div',{staticClass:"number",attrs:{"data-number":"07"}},[_c('b',{staticClass:"ps-2"},[_vm._v("Mamíferos")]),_c('p',{staticClass:"ps-2 mb-0 mt-2"},[_vm._v("Los mamíferos, a pesar de no ser un grupo grande, ocupan todos los ambientes habitables de la tierra. Son el grupo con mayor diferenciación dentro del reino animal, son muy diversos en cuanto a tamaño, formas y funciones, sus cuerpos están cubiertos por pelos (reducidos en algunos) poseen varios tipos de glándulas (sudoríparas, odoríferas, sebáceas y mamarias), cuatro extremidades adaptadas para muchos tipos de locomoción, fecundación interna desarrollada en el útero con una unión placentaria y juveniles comúnmente alimentados con leche de las glándulas mamarias. Poseen pieles gruesas que están protegidas por el pelo. Su alimentación es variada e incluye a los insectívoros, que se alimentan principalmente de pequeños invertebrados como lombrices y caracoles; los herbívoros que poseen adaptaciones en sus molares para la trituración de plantas; los carnívoros quienes cuentan con dientes perforadores y cortantes para desgarrar a sus presas y con garras afiladas para capturarlas más fácil; y los omnívoros que comen alimentos de origen animal y vegetal indistintamente. Están representados por los ornitorrincos, zarigüeyas, canguros, koalas, erizos, topos, lémures, murciélagos, simios, hombres, armadillos, hormigueros, conejos, ardillas, delfines, ballenas, perros, lobos, gatos, osos, leones marinos, vacas marinas, manatíes, elefantes, caballos, cerdos, camellos, hipopótamos, jirafas, ovejas, bovinos, entre otros.")])])]),_c('div',{staticClass:"col-lg-4"},[_c('figure',[_c('img',{attrs:{"src":require("@/assets/curso/temas/Tema1/img10.png")}})])])])])],1),_c('h3',{staticClass:"mt-5"},[_vm._v("Comportamiento, alimentación y reproducción")]),_vm._m(9),_c('div',{staticClass:"container tarjeta p-2"},[_c('SlyderB',{staticClass:"ps-4 mb-0 imgslider2",attrs:{"datos":_vm.datosSlyder}})],1),_vm._m(10),_c('h3',{staticClass:"mt-5"},[_vm._v("Marco Normativo")]),_c('p',[_vm._v("En Colombia, las normativas que propenden por el cuidado de la fauna silvestre, se han venido promulgando desde el año 1972, cuando se fundaron las Juntas Defensoras de Animal